import React from 'react';
import ReactDOM from 'react-dom';
import { BrowserRouter } from 'react-router-dom';

import './index.css';
import './assets/fonts/SourceCodePro-Italic.ttf';
import './assets/fonts/SourceCodePro-Regular.ttf';
import './assets/fonts/SourceCodePro-Bold.ttf';
import './assets/fonts/Electrolize/Electrolize-Regular.ttf';
import App from './App';
import * as serviceWorker from './serviceWorker';
import { Provider } from 'react-redux';
import { createStore, applyMiddleware, compose, combineReducers } from 'redux';
import thunk from 'redux-thunk';

import scrollReducer from './store/reducers/scroll';
import mailReducer from './store/reducers/mail';
import screenReducer from './store/reducers/screen';

// const composeEnhancers = process.env.NODE_ENV === 'development' ? window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ : null || compose;

const rootReducer = combineReducers({
  scroll: scrollReducer,
  mail: mailReducer,
  screen: screenReducer
})

const store = createStore(rootReducer, compose(
  applyMiddleware(thunk)
));

ReactDOM.render(
  <Provider store={store} >
  <BrowserRouter>
    <App />
  </BrowserRouter>
  </Provider>,
  document.getElementById('root')
);

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
